import  './date.js'
import Vue from 'vue'

var Utils = {}
Utils.install = function (Vue, options) {
  // 1. 添加全局方法或属性
  Vue.prototype.$goToRoute = function (path) {
    this.$router.push(path)
  }
  Vue.prototype.$isRouteIn = function (path) {
    var reg = new RegExp("^" + path + "(/|$)");
    return reg.test(this.$route.path)
  }
}
Vue.use(Utils)



