<template>
  <div style="font-size:.6rem;">
      {{content}}
  </div>
</template>

<script>
  export default{
    data: function () {
      return {
        content: ''
      }
    },
    methods: {
      getRelease: function () {
        var that = this
        that.$store.commit('load',true)
        this.$http.get('mock/flight/xxx/release').then(response => {
          that.$store.commit('load',false)
          return response.json()
        }).then(json=>{
          that.content = json.data

        });
      }
    },
    mounted: function () {
      this.getRelease();
    }

  }
</script>



