import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home/Home.vue'
import Flight from '@/views/flight/Flight.vue'
import Message from '@/views/message/Message.vue'

import FlightDetail from '@/views/flight/Detail.vue'
import Vip from '@/views/flight/children/Vip.vue'
import Release from '@/views/flight/children/Release.vue'
import Notam from '@/views/flight/children/Notam.vue'
import Crew from '@/views/flight/children/Crew.vue'
import Airport from '@/views/flight/children/Airport.vue'
import Passenger from '@/views/flight/children/Passenger.vue'
import Support from '@/views/flight/children/Support.vue'


import More from '@/views/more/More.vue'
import Button from '@/views/more/Button.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home
    },{
      path: '/flight',
      component: Flight,
    },
    {
      path: '/flight/:id',
      component: FlightDetail,
      children:[{
        path:'vip',
        component:Vip
      },{
        path:'airport',
        component:Airport,
      },{
        path:'support',
        component:Support,
      },{
        path:'crew',
        component:Crew,
      },{
        path:'notam',
        component:Notam,
      },{
        path:'release',
        component:Release,
      },{
        path:'passenger',
        component:Passenger,
      }]
    },
    {
      path: '/message',
      component: Message
    },{
      path: '/more',
      component: More
    },{
      path: '/button',
      component: Button
    }
  ]
})



