<template>
  <div class="xa-page  ">
    <head-top title="航班动态"></head-top>
    <div>
      <div class="flight-header">
        <div class="flight-header__nav xa-nav" style="display:flex;">
          <a :class="{active: filter=='out'}" @click="filter='out'">出港</a>
          <a :class="{active: filter=='inn'}" @click="filter='inn'">进港</a>
          <a :class="{active: filter=='care'}" @click="filter='care'">关注</a>
          <a :class="{active: filter=='vip'}" @click="filter='vip'">要客</a>
        </div>
        <div style="display:flex;font-size:0.6rem;font-weight:bold;text-align: center;height:1.2rem;
        border-top:1px solid #ECECEC;border-bottom:1px solid #ECECEC;line-height: 1.2rem;">
          <div style="flex:1">航班</div>
          <div style="flex:1">航线</div>
          <div style="flex:1">机位</div>
          <div style="flex:1">起飞</div>
          <div style="flex:1">到达</div>
          <div style="flex:1">状态</div>
        </div>
      </div>
      <scroller :on-refresh="refresh" class="flight-content">
          <a v-for="f in flightsOnShow" class="flight" @click="viewFlightDetail(f)" style="display:flex">
            <div style="flex:1">{{f.FlightNo}}<br>{{f.AcReg}}</div>
            <div style="flex:1">{{f.Depa}}<br>{{f.Arri}}</div>
            <div style="flex:1">{{f.StandOut}}</div>
            <div style="flex:1">
              <span class="st">{{new Date(f.Std).toString("HH:mm")}}</span><br>
              <span v-if="f.Etd&&!f.Atd" class="et">{{new Date(f.Etd).toString("HH:mm")}}</span>
              <span v-if="f.Atd" class="at">{{new Date(f.Atd).toString("HH:mm")}}</span>
              <span v-if="!f.Etd&&!f.Atd">&nbsp;</span>
            </div>
            <div style="flex:1">
              <span class="st">{{new Date(f.Sta).toString("HH:mm")}}</span><br>
              <span v-if="f.Eta&&!f.Ata" class="et">{{new Date(f.Eta).toString("HH:mm")}}</span>
              <span v-if="f.Ata" class="at">{{new Date(f.Ata).toString("HH:mm")}}</span>
              <span v-if="!f.Eta&&!f.Ata">&nbsp;</span>
            </div>
            <div style="flex:1">起飞</div>
          </a>
      </scroller>
    </div>
    <footer-guide></footer-guide>
  </div>
</template>


<script>
  import footerGuide from '@/components/Footer'
  import headTop from '@/components/Header'
  export default{
    data(){
      return {
        flightsOnShow: [],
        flights: [],
        filter: 'out'
      }
    },
    methods: {
      refresh:function(done){
          setTimeout(() => {
            done()
          }, 1500)
      },
      log: function (e) {
        console.log(e)
      },
      getFlights: function () {
        this.$http.get('mock/flight/date/2017-7-18').then(response => {
          return response.json()
        }).then(json=>{
          this.flights = json
        });
      },
      viewFlightDetail: function (f) {
        this.$router.push("/flight/xxx/support")
      },
      filterFlights: function(val){
        if (!val){
          this.flightsOnShow = this.flights;
        }
        if (val == 'out') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Depa == "ZSAM"
          })
        }
        if (val == 'inn') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Arri == "ZSAM"
          })
        }
        if (val == 'care') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Care
          })
        }
        if (val == 'vip') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Vip=='Y'
          })
        }
      }
    },
    watch: {
      flights:function(val,oldVal){
        this.filterFlights(this.filter)
      },
      filter: function (val, oldVal) {
        this.filterFlights(val)
      }
    },
    components: {
      footerGuide,
      headTop
    },
    created: function () {
      if(this.flights.length==0){
        this.getFlights()
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";
  table {
    color: #333333;
    table-layout: fixed;
    border-collapse: collapse;
    font-family: 黑体;
    width: 100%;
    text-align: center;
  }

  tr {
    border-bottom: 1px solid #ECECEC;
  }

  .flight-header__table > tr {
    border-top: 1px solid #ECECEC;
  }

  th, td {
    padding: 0.2rem 0.2rem;
  }

  .flight-header {
    background-color: #fff;
    margin-top:$XaHeaderHeight;
    width: 100%;
    z-index: 100;
    position: fixed;
    left: 0;
    font-size: 0.6rem;
    &__table {
      width: 100%;
      height: 1.2rem;
    }
  }

  .flight-content {
    font-size: 0.6rem;
    margin-top: 4.4rem;
    height:calc(100% - 4.4rem);
    width:100%;
    text-align: center;
    background-color:#fff;
    z-index:-1;
  }
  .flight{
    border-bottom: 1px solid #ECECEC;
    padding:.2rem 0;
    &:active{
      background-color: $XaColorLighterBlue;
    }
  }
  .st {
    color:$XaStFontColor;
  }
  .et {
    color:#994500;
  }
  .at {
    color:$XaColorDarkerBlue;
  }
</style>



