    <div class="xa-page fd">
        <head-top back="/flight" title="MF8401">
            <span class="xa-icon care">&#xe6d2;</span>
        </head-top>
        <scroller style="padding-top:2rem;" :on-refresh="refresh">
            <div class="fd-general">
                <div class="fd-general__element">
                    <div class="fd-general__element__line1 fd-airport">厦门</div>
                    <div class="fd-general__element__line2 fd-time">15:00</div>
                    <div class="fd-general__element__line3 fd-light">计划<span>1500</span></div>
                </div>
                <div class="fd-general__element__middle">
                    <div class="fd-general__element__line1 fd-light">73/24</div>
                    <div class="fd-general__element__line2">
                        <div style="height:2px;position:relative;background-color: #aaa;top:50%;">
                            <div style="height:2px;width:45%;background-color:#C7E0F4"></div>
                            <div class="icon xa-icon fd-plane">
                                &#xe609;</div>
                        </div>
                    </div>
                    <div class="fd-general__element__line3 fd-light">B-2129/787</div>
                </div>
                <div class="fd-general__element">
                    <div class="fd-general__element__line1 fd-airport">北京</div>
                    <div class="fd-general__element__line2 fd-time">18:00</div>
                    <div class="fd-general__element__line3 fd-light">计划<span>1500</span></div>
                </div>
            </div>
            <div class="fd-line">
                <div class="fd-line__eobt">EOBT : 15:00</div>
                <div class="fd-line__cobt">COBT : 15:00</div>
                <div class="fd-line__cobt"></div>
                <div class="fd-line__flg">要客 特服</div>
            </div>
            <div class="fd-related">
                <a class="fd-related__pre">
                    <div class="arrow">前序</div>
