                <div class="fd-delay__reason">
                    延误原因:旅客<br>
                    对外原因:旅客<br>
                    原因说明:等待晚到旅客<br>
                </div>
            </div>
            <div class="fd-spt">
                <div class="fd-spt__time">
                    <div>上客<br>13:00</div>
                </div>
                <div class="fd-spt__time">
                    <div>客齐<br>13:00</div>
                </div>
                <div class="fd-spt__time">
                    <div>关货舱<br>13:00</div>
                </div>
                <div class="fd-spt__time">
                    <div>关客舱<br>13:00</div>
                </div>
                <div class="fd-spt__time">
                    <div>撤轮档<br>13:00</div>
                </div>
            </div>
