    </div>
</template>
<script>
    import headTop from '@/components/Header'
    var menus = [
        {name: 'support', title: '保障信息'},
        {name: 'notam', title: '航行通告'},
        {name: 'release', title: '放行资料'},
        {name: 'airport', title: '机场信息'},
        {name: 'crew', title: '机组信息'},
        {name: 'vip', title: '要客信息'},
        {name: 'passenger', title: '旅客信息'}
    ]
    export default{
        data: function () {
            return {
                menus: menus
            }
        },
        methods: {
            refresh: function (done) {
                setTimeout(() => {
                    done()
                }, 1500)
            },
        },
        components: {
            headTop
        }
    }

