<style lang="scss" rel="stylesheet/scss" scoped>
    @import "src/style/base.scss";

    .xa-page{
        background-color:#f2f2f2;
        color:#555;
    }
    .care{
        font-size:1rem;
    }
    .fd-general {
        width: 100%;
        display: flex;
        padding: 0.3rem;
        text-align: center;
        background-color: #fff;
        border-bottom: 1px solid $XaBorderGray;
        color: $XaColorDarkerBlue;
        z-index: 800;
        &__element{
            flex: 4;
            height: 4.4rem;
            &__line1{
                height: 1.4rem;
                line-height: 1.4rem;
            }
            &__line2{
                line-height: 2.0rem;
                height: 2.0rem;
            }
            &__line3{
                height: 1.0rem;
                line-height: 1.0rem;
            }
        }
        &__element__middle{
            flex: 3;
            height: 4.4rem;
        }
    }

    .fd-line{
        display:flex;
        font-size:.6rem;
        height:1.4rem;
        line-height:1.4rem;
        text-align: center;
        &__eobt,&__cobt{
            flex:1;
        }
        &__flg{
            flex:1;
        }
    }
    .fd-related{
        height:2.6rem;
        display:flex;
        border-bottom:1px solid $XaBorderGray;
        font-size:.6rem;
        color:$XaTextGrayColor;
        background-color: #fff;
        &__pre{
            display: flex;
            flex:1;
            padding:.3rem .3rem;
            border-right:1px solid $XaBorderGray;
            & .arrow{
                flex:1;line-height:2rem;
                text-align: center;
                &:before{
                    content: " ";
                    @include setArrow(0.4rem, $XaBorderGray, 2px,left);
                }
            }
            & .flight{
                flex:1;line-height:1rem;
                text-align: left;
            }
        }
        &__aft{
            display: flex;
            flex:1;
            padding:.3rem .3rem;
            & .arrow{
                flex:1;line-height:2rem;
                text-align: center;
                &:after{
                    content: " ";
                    @include setArrow(0.4rem, $XaBorderGray, 2px,right);
                }
            }
            & .flight{
                flex:1;line-height:1rem;
                text-align: left;
            }
        }
    }
    .fd-content {
        padding: .3rem;
        background-color: #fff;
    }
    .fd-delay{
        display:flex;
        margin-top:.4rem;
        background-color: #fff;
        &__time{
            flex:2;
            border-right:1px solid $XaBorderGray;
            padding:.8rem;
            text-align: center;
        }
        &__reason{
            flex:5;
            padding:.8rem;
        }
    }
    .fd-spt{
        display: flex;
        margin-top:.4rem;
        height:2.4rem;
        background-color: #fff;
        &__time{
            padding:.4rem 0;
            flex:1;
            text-align: center;
            &>div{
                border-right:1px solid $XaBorderGray;
            }
            &:last-child{
                border-right:none;
            }
        }
    }

    .fd-plane{
        position:absolute;
        left:45%;
        font-size:1rem;
        height:1.5rem;
        width:1.5rem;
        margin-top:-1rem;
        margin-left:-0.5rem;
        color:$XaColorAirBlue;
    }

    .fd-airport {
        font-size: 1.0rem;
        color:$XaColorDarkBlue;
    }

    .fd-time {
        font-size: 1.4rem;
        color:$XaColorDarkerBlue;
    }

    .fd-light {
        font-size: 0.6rem;
        color: $XaTextGrayColor;
    }


    .st {
        color: #c41a16;
    }


</style>



