<template>
  <div>
    <header id='header-nav'>
      <div class="header-back" v-if="back" @click="$goToRoute(back)"></div>
      <div class="header-title">
        {{title}}
      </div>
      <div class="header-right">
        <slot></slot>
      </div>
    </header>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {}
    },
    props:['back','title'],
    methods:{
      goBack:function(){
        $route.push(back)
      }
    }
  }

</script>

<style lang="scss" rel="stylesheet/scss" >
  @import "src/style/base.scss";
  #header-nav{
    color:#fff;
    background-color: $XaColorAirBlue;
    position: fixed;
    z-index: 100;
    left: 0;
    width:100%;
    height:$XaHeaderHeight
  }
  .header-back{
    position:absolute;
    left:0;
    top:0;
    width:2rem;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    text-align:center;
    &:after{
      content: " ";
      @include setArrow(0.4rem, #fff, 2px,left);
    }
  }
  .header-right{
    position:absolute;
    right:0;
    top:0;
    width:2rem;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    text-align:center;
  }
  .header-title{
    width:100%;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    font-size:0.8rem;
    color:#fff;
    text-align: center;
  }
</style>



